@import url(https://fonts.googleapis.com/css2?family=Sora:wght@100;200;300;400;500;600;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


html {
  font-size: 16px;
  font-family: Helvetica;
  background-color: #211D27;
}
@media screen and (min-width: 500px) {
  .container {
    width: 100%;
    display: flex;
    justify-content: center;
  }
  
  .Rank {
    display: grid;
    grid-template-rows: auto;
    grid-template-columns: 1fr;
    align-items: center;
    justify-items: center;
    margin-top: 80px;
    overflow:visible;
    width: 850px;
  }

  .filters {
    grid-column: span 3;
    display: flex;
    width: 100%;
    justify-content: flex-start;
    grid-gap: 16px;
    gap: 16px;
  }

  .MuiButton-root {
    display: none !important;
  }

  .filter-drawer {
    display: none;
  }

}

@media screen and (max-width: 500px) {

  :root {
    --screen-height: -webkit-fill-available
  } 
  
  .Rank {
    display: grid;
    height: calc(100% - 16px);
    grid-template-rows: 30px 1fr;
    grid-template-columns: 400px;
    grid-row-gap: 24px;
    row-gap: 24px;
    justify-content: center;
    align-items: center;
    justify-items: center;
    padding-top: 16px;
  }

  .filters {
    grid-column: span 1;
    justify-self: end;
    display: flex;
    justify-content: flex-end;
    grid-gap: 16px;
    gap: 16px;
  }

  #artist-filter {
    display: none;
  }

  #year-filter {
    display: none;
  }

  #ranking-filter {
    display: none;
  }

  .container {
    box-sizing: border-box;
    max-height: calc(100% - 16px);
  }

  .MuiButton-root {
    margin-right: 24px !important;
  }

  #rank-page-container {
    height: -webkit-fill-available;
    max-height: 97vh;
  }

  .filter-drawer {
    position: fixed;
    top: 100%;
    bottom: 0;
    width: 100%;
    height: -webkit-fill-available;
    background-color: #211D27;
    border-radius: 20px 20px 0 0;
    transition: all 0.3s ease-out;
    z-index: 1000;
  }

  .filter-drawer-content {
    padding: 40px 24px 0px 24px;
    height: 100%;
  }

  .filter-drawer-header {
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
  }

  .filter-drawer-header-title {
    justify-self: center;
    margin-left: 40px;
    font-size: 20px;
    color: #CFCFCF;
    font-weight: 600;
  }

  .filter-drawer-body {
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    justify-content: baseline;
    align-items: stretch;
    width: 100%;
    height: -webkit-fill-available;
    overflow: auto;
    grid-gap: 24px;
    gap: 24px;
  }

  #artist-filter-drawer .searchWrapper {
    min-height: 40px !important;
    height: 48px;
    min-width: 108px;
    border: 1px solid #B1A5FA;
    border-radius: 24px !important;
    max-width: 100%;
    align-content: center;
    padding-left: 12px !important;
    align-items: center;
    transition: all 0.5s;
    margin-bottom: 8px;
  }
  
  #artist-filter-drawer .optionContainer {
    font-size: 16px;
    max-height: 184px;
  }

  #artist-filter-drawer .icon_down_dir {
    right: 24px;
  }

  #artist-filter-drawer_input {
    margin-top: 0px;
  }

  #artist-filter-drawer .chip {
    border-radius: 20px !important;
    background-color: #897ECC !important;
    margin-bottom: 2px !important;
    color: #FFFFFF !important;
  }

  #artist-filter-drawer .option:hover {
    background-color: #897ECC50 !important;
    color: #CFCFCF !important;
}

  #year-filter-drawer {
    width: 100%;
  }

  #year-filter-drawer .year-filter-container {
    max-width: 100%;
    height: 48px;
    border-radius: 24px;
  }

  #year-filter-drawer .calendar-wrapper {
    width: calc(100% - 48px);
    padding-right: 0px;
    position: absolute;
    left: 24px;
    right: 24px;
  }

  #year-filter-drawer .carousal {
    width: 100%;
  }

  #year-filter-drawer .chip-wrapper {
    padding-bottom: 0px;
    padding-top: 5px;
    padding-left: 12px;
  }

  #year-filter-drawer-container {
    align-items: center;
  }

  #year-filter-drawer .MuiSvgIcon-root {
    margin-right: 8px !important;
  }

  #ranking-filter-drawer .ranking-filter-container {
    max-width: 100%;
    height: 48px;
    border-radius: 24px;
  }

  #ranking-filter-drawer .ranking-slider {
    width: calc(100% - 106px);
    padding-right: 32px;
    position: absolute;
    left: 24px;
    right: 24px;
    padding-left: 24px;
  }

  #ranking-filter-drawer .chip-wrapper {
    padding-bottom: 0px;
    padding-top: 5px;
    padding-left: 12px;
  }

  #ranking-filter-drawer .chip-container {
    width: 100%;
    justify-content: space-between;
  }

  #ranking-filter-drawer-icon {
    margin-right: 8px !important;
    margin-top: -1px;
  }

  #ranking-filter-drawer-icon-chip {
    margin-top: 7px;
    margin-right: 16px !important;
  }

}

@media screen and (min-width: 1024px) {   

    .album {
        width: 300px;
        height: 375px;
        border-radius: 10px;
        background: linear-gradient(20deg, var(--bg-color) 15%, var(--bg-gradient));
        transition: all 0.5s;
    }

    .album:hover {
        box-shadow: 0px 0px 20px 0px var(--shadow);
        cursor: pointer;
    }

    .album img {
        margin-top: 15px;
        border-radius: 5px;
        width: 270px;
        height: 270px;
        box-shadow: 0px 0px 25px 0px rgba(0, 0, 0, .25);
        margin-left: 15px;
    }

    .album-title {
        font-size: 1.25rem;
        margin-top: 12px;
        margin-bottom: 4px;
        text-align: left;
        padding-left: 15px;
        padding-right: 15px;
        font-weight: 600;
        color: var(--text-color-1);
        max-width: 300px;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
    }

    .album-artist {
        font-size: 1.25rem;
        margin-top: 4px;
        text-align: left;
        padding-left: 15px;
        font-weight: 400;
        color: var(--text-color-2);
    }

}

