@media screen and (max-width: 500px) {   

    .album {
        aspect-ratio: 4/5;
        height: 100%;
        border-radius: 10px;
        background: linear-gradient(20deg, var(--bg-color) 15%, var(--bg-gradient));
        transition: all 0.5s;
        overflow: hidden;
    }

    .album:hover {
        box-shadow: 0px 0px 20px 0px var(--shadow);
        cursor: pointer;
    }

    .album img {
        margin-top: 4%;
        border-radius: 5px;
        aspect-ratio: 1/1;
        height: 72%;
        box-shadow: 0px 0px 25px 0px rgba(0, 0, 0, .25);
        margin-left: 5%;
    }

    .album-title {
        font-size: 2.25vh;
        margin-top: 8px;
        text-align: left;
        padding-left: 15px;
        padding-right: 15px;
        font-weight: 600;
        color: var(--text-color-1);
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        -webkit-margin-after: 0;
                margin-block-end: 0;
        max-width: 90%;
    }

    .album-artist {
        font-size: 2.25vh;
        max-width: 90%;
        text-align: left;
        padding-left: 15px;
        font-weight: 400;
        color: var(--text-color-2);
        -webkit-margin-before: 0;
                margin-block-start: 0;
        text-overflow: ellipsis;
    }

}
@media screen and (min-width: 1024px) {
    
    .album-container {
        display: grid;
        grid-template-rows: 450px;
        grid-template-columns: 300px 250px 300px;
        align-items: center;
        justify-items: center;
    }

    h1 {
        color: white;
        font-size: 24px;
        grid-column: span 3;
    }

    .skip {
        background-color: transparent;
        color: #CFCFCF;
        height: 40px;
        border: 1px solid #B1A5FA;
        border-radius: 20px;
        font-size: 18px;
        transition: all 0.5s;
        grid-row-start: 2;
        padding-left: 30px;
        padding-right: 30px;
    }

    .skip:hover:enabled {
        cursor:pointer;
        background-color: #362F40;
        color: #FFFFFF
    }

    .skip:disabled {
        border-color: darkgrey;
        color: darkgrey;
        cursor:default;
    }

    #first:before {
        content: "Skip"
    }

    #second:before {
        content: "Skip"
    }

    .loader {
        width: 48px;
        height: 48px;
        border-radius: 50%;
        display: inline-block;
        position: relative;
        background: linear-gradient(0deg, #675F99 33%, #AB9EFF 100%);
        box-sizing: border-box;
        -webkit-animation: rotation 1s linear infinite;
                animation: rotation 1s linear infinite;
    }
    .loader::after {
        content: '';  
        box-sizing: border-box;
        position: absolute;
        left: 50%;
        top: 50%;
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
        width: 44px;
        height: 44px;
        border-radius: 50%;
        background: #211D27;
    }
    @-webkit-keyframes rotation {
        0% { -webkit-transform: rotate(0deg); transform: rotate(0deg) }
        100% { -webkit-transform: rotate(360deg); transform: rotate(360deg)}
    }
    @keyframes rotation {
        0% { -webkit-transform: rotate(0deg); transform: rotate(0deg) }
        100% { -webkit-transform: rotate(360deg); transform: rotate(360deg)}
    } 

    span {
        grid-column: span 3;
        grid-row-start: 1;
    }

    .skip-error-message {
        color: white;
        visibility: hidden;
        background: linear-gradient(267.35deg, #362F40 0%, rgba(54, 47, 64, 0.22) 102.71%);
        border-radius: 5px;
        width: 300px;
        height: 60px;
        line-height: 60px;
        opacity: 80%;
        transition: all 0.5s;
        text-align: center;
    }

    .skip-error-message:hover {
        opacity: 100%;
    }

    .skip-error-both {
        min-width: 250px;
        height: 60px;
        background: linear-gradient(267.35deg, #362F40 0%, rgba(54, 47, 64, 0.22) 102.71%);
        border-radius: 5px;
        justify-self: center;
        align-self: center;
        z-index: 1;
        color: #CFCFCF;
        line-height: 80px;
        visibility: hidden;
        transition: opacity 2s ease-in;
        transition-delay: 1s;
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .is-visible {
        visibility: visible;
        opacity: 0;
    } 


    #album2 {
        grid-column-start: 3;
    }

    .albums {
        grid-column: span 3;
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
    }

}

